}

form input[type="submit"]:hover {
    background-color: rgb(223, 72, 59);
}
form input[type="submit"]:active {
    width: 6.5em;
}

#follow-us {
    margin-top: 5em;
}
.center-alingnment {
    width: 80%;
}

.modal-container {
    padding-bottom: 20px;
}
.modal-container li{
    text-align: left;
}
.modal-content {
    border: 2px solid #BF2200;
    margin: 20px;
    width: auto !important;
    border-radius: 15px;
}

@media only screen and (max-width: 600px) {
    body {
        overflow-x: hidden;
    }
    h3{
        font-size: 39px !important;
    }
    #home {
        margin-top: 0;
    }
    .iedc_img {
        margin: 0 auto;
        width: 15em;
        height: auto;
    }
    .icon-link{
        font-size: 2.5rem;
    }
    .illustration{
        object-fit: cover;
        margin-top: -6em;
    }
    #home>h2{
        font-size: 39px;
    }
    .officer-content>h3{
        text-align: center;
        font-size: 25px !important;
    }
    .officer-content>h4 {
        text-align: center;
        font-size: 19px !important;
    }
    .officer-img{
        height: 250px !important;
        width: 250px !important;
    }
    .mobile-text>p{
        font-size: 15px;
    }
    #box_curve_bottom {
        border-bottom-left-radius: 10%;
        border-bottom-right-radius: 10%;
    }
    #box_curve_top {
        border-top-left-radius: 10%;
        border-top-right-radius: 15%;
        /* padding-top: 40px; */
    }
    #centre_mobile {
        text-align: left;
        margin-left: 30%;
    }
    .mobile_space {
        margin-bottom: 0px;
    }
    .margin_top_mobile {
        margin-top: 0em;
    }

    #contact {
        margin-top: 0;
        padding-top: 2em;
        box-shadow: none;
    }
    #follow-us {
        padding-left: 0;
        margin-top: 0;
    }
    #icons {
        margin-left: -20px;
    }

    .about-section {
        border-radius: 50px 50px 0 0;
        padding-bottom: 10px;
    }
}

